@import url("https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");

@mixin subhead($fz, $fw, $lh) {
  font-size: $fz;
  font-weight: $fw;
  line-height: $lh;
}

@mixin pchange($fz, $fw, $lh) {
  font-size: $fz;
  font-weight: $fw;
  line-height: $lh;
}

html,
body {
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;

  font-family: "Noto Sans KR", "Apple SD Gothic Neo", Sans-serif;
  color: #000;
  letter-spacing: -0.6px;

  box-sizing: border-box;
}

body,
header,
section,
article,
footer,
nav,
aside,
mark,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
a,
img,
dl,
dt,
dd,
address,
code,
em,
strong,
blockquote,
q {
  margin: 0;
  padding: 0;
}

section {
  padding: 0 20px 20px;
}

ul,
ol,
li {
  list-style: none;
}
q {
  quotes: none;
}
em,
address {
  font-style: normal;
}
hr {
  display: none;
}

a:link {
  color: #10212e;
  text-decoration: none;
}

a:visited {
  color: #10212e;
}

a:active {
  color: #10212e;
}

button {
  background: none;
  border: none;
  cursor: pointer;
}

table {
  border-collapse: collapse;
}

td,
th,
td {
  padding: 15px 0;
  border-bottom: 1px solid #c4c4c4;
}

th {
  @include subhead(16px, 700, 24px);
}

td {
  @include pchange(14px, 400, 24px);
}

h1 {
  font-size: 40px;
  font-weight: 700;
  line-height: 55px;
}

h2 {
  font-size: 34px;
  font-weight: 700;
  line-height: 45px;
}

h3 {
  font-size: 30px;
  font-weight: 700;
  line-height: 40px;
}

h4 {
  font-size: 24px;
  font-weight: 700;
  line-height: 35px;
}

h5 {
  font-size: 22px;
  font-weight: 700;
  line-height: 30px;
}

h6 {
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}

p {
  font-size: 14px;
  font-weight: 400;
  line-height: 24px;
}

fieldset {
  margin: 0;
  padding: 0;
  border: none;

  legend {
    display: none;
  }
}

.useGuide {
  margin: 50px auto;
  text-align: center;

  h3 {
    margin-bottom: 40px;
  }

  > .guideWrap {
    display: flex;
    justify-content: space-between;
    gap: 20px;

    > .tabs {
      display: flex;
      justify-content: space-between;
      gap: 20px;
      width: 100%;

      > .guide_btn {
        padding: 10px 20px;

        background-color: rgb(225, 225, 225);
        border-radius: 10px;
        cursor: pointer;

        // &.on {
        //   color: #000;
        // }
      }
    }
  }
}
